.Card {
    display:inline-block;
    flex-direction: column;
    width: 300px;
    height: 300px;
    align-items: center;
    text-align: center;
    background-color: #6e9b51;
    border-radius: 25px; 
    padding-bottom: 20px;    
    text-decoration: none;
    color: white;
    margin-right: 10%;
    margin-bottom: 5%;   
    
}

.Card:hover {
  transform: scale(1.02);
}
.Card img {
  width: 300px;
  height: 180px;
  border-radius: 25px 25px 0 0;
  margin: 0;
}
.nameDiv {
  text-decoration: none;
  font-size: 1rem;
  align-self: center;
  padding: 0 15px;
}
.nameDiv:hover {
  color: black;
}

.Card .diets {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  gap: 10px;
  padding: 0 15px;
}

.diettitle {
  width: 100%;
  font-weight: bold;
  margin: 5px 0 0;
  font-size: 0.8rem;
}
.Card .healthScore {
  width: 100%;
  margin: 0;
  font-size: 0.8rem;
}



