.navBar {
    background-color: #6e9b55;
    width: 100%;
    height: 60px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
}

.navBar .logo {
    display: flex;
    align-items: center;
    width: 80px;    
}

.navBar .logo img {
    width: 60%;
    border: none;
    border-radius: 4px;    
}

.navBar .srchBar{
    flex: 45%;
    display: flex;
    padding-right: 20px;
    justify-content: flex-end;
}

.navBar .navLinks {
    flex: 45%;
    display: flex;
    justify-content: left;
    align-items: center;
    padding-left: 2%;
}

.navBar .navLinks .Links:hover {
    color: black;
}

.navBar .navLinks .Links {
    max-height: 80px;
    text-decoration: none;
    color: white;
    font-size: 20px;
    margin-right: 15px;
}

.navBar .navLinks button {
    display: none;
    margin-left: 20px;
}


@media only screen and (max-width: 850px) {
    .navBar .navLinks .Links {
        font-size: 15px;
    }
}

@media only screen and (max-width: 750px) {
    .navBar .navLinks .Links {
        font-size: 10px;
    }
}
