.landingDiv {    
    position: relative;
    width: 100vw;
    height: 100vh;
    background-image: url(../assets/LP-bg.jpg);
    display: flex;
    background-size: cover;
    justify-content: center;
    align-items: center;        
}

.landingCard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px 15px 20px;
    align-items: center;
    background-color: rgb(17, 16, 12);
    opacity: 0.8;
    border-radius: 20px;    
}

.landingCard h1 {
    color: white;
}

.landingBtnDiv{
    width: 35%;
    display: flex;
    justify-content: center;    
    text-decoration: none;
}

.landingBtnDiv button {    
    background-color: #73a838;
    padding: 5px;
    border-radius: 20px;
    font-weight: bolder;
    cursor: pointer;
}

.landingBtnDiv button:hover {
    background-color: white;
    color: #bde643;
}

